<template>
  <div class="collect-page">
<van-nav-bar fixed title="我的收藏" />
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      @load="onLoad"
    >
      <ArticleItem v-for="item in list" :key="item.id" :item="item" />
    </van-list>
  </div>
</template>

<script>
// 导入请求的函数
import { getArticlesCollect } from '@/api/article'
export default {

  name: 'CollectPage',
  data () {
    return {
      list: [],
      loading: false,
      finished: false,
      page: 1
    }
  },
  methods: {
    async onLoad () {
      const res = await getArticlesCollect({
        page: this.page
      })
      // console.log(res)

      this.list.push(...res.data.rows)
      this.loading = false
      this.page++
      // 如果 current 的值 已经要大于等于res.data.pageTotal
      // 需要将 finished 的值设置为 true 表示已经加载完了
      if (this.page > res.data.pageTotal) {
        this.finished = true
      }
    }
  }
}
</script>

<style lang="less" scoped>
.collect-page {
  margin-bottom: 50px;
  margin-top: 44px;
}
</style>
